import React, { Component } from "react";
import { Grid, Row, Col,   } from "react-bootstrap";
import { DateTime, Interval } from "luxon";

import ControlPanel from "./ControlPanel";
import Table from "./Table";

class Schedule extends Component {

    constructor(props) {
        super(props);
        this.state = {
            termSelected: -1,
            days: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
            intervals: (function() {
                return Interval.fromDateTimes(DateTime.fromObject({ hours: 6 }), 
                DateTime.fromObject({ hours: 23, minutes: 59 })).splitBy({ hours: 1 })
                // let i = 0;
                // let times = [];
                // let startTime = DateTime.fromObject({ hours: 6 });
                // for(; i < 7; i++) {
                //     times.push(startTime.plus({ hours: 1 }));
                // }
                // return times;
            })()
        }
        this.selectTerm = this.selectTerm.bind(this);
    }

    selectTerm(term_id) {
        this.props.selectTerm(term_id);
    }

    render() {
        return (
            <div>
                <div className="panel panel-default">
                    <div className="panel-body">
                    <ControlPanel selectTerm={this.selectTerm} terms={this.props.terms} />
                    <Table days={this.state.days} intervals={this.state.intervals} />
                    </div>
                </div>
            </div>
        )
    }


}

export default Schedule;





